@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  justify-content: center;
  background-image: url(/mywebsite/static/media/desolate.c9e157ab.svg); /*linear-gradient(left, #4b79a1,#283e51 );*/
  background-attachment: fixed;
  background-size: cover;
  background-position:center;
  text-align: center;
  background-repeat: no-repeat;
  background-color: rgb(232, 209, 209); /* rgb(71, 123, 132); */
  background-blend-mode: color-burn;
  font-family: 'Merriweather', serif;
  /* overflow: hidden; */
  /* min-height: fit-content; */
  /* display: flex; */
}

.endbar{
  width: 100%;
  height: 110px; 
  background-color:rgb(19, 15, 15);
}

.down-icon{
  width: 32px;
  height: 32px;
  position: absolute;
  -webkit-filter: brightness(2.5);
          filter: brightness(2.5);
  background-image: url(/mywebsite/static/media/arrow_d.a9888955.svg);
  background-size: cover;
  top: 92%;
  left: 50%; 
  margin-left: -16px;
}
.down-icon:hover{
  -webkit-filter: brightness(3.0);
          filter: brightness(3.0);
}

@media only screen and (max-height: 450px){
  .down-icon{
    top: 89%;
  }
}
.up-icon{
  width: 44px;
  height: 44px;
  position: absolute;
  -webkit-filter: brightness(2.5);
          filter: brightness(2.5);
  background-image: url(/mywebsite/static/media/arrow_u.dd423725.svg);
  background-size: cover;
  margin-top: -22px;
  left: 50%; 
  margin-left: -22px;
}
.up-icon:hover{
  -webkit-filter: brightness(3.0);
          filter: brightness(3.0);
}

@media screen and (min-width: 1200px){
  .icon{
    width: 40px;
    height: 40px;
    margin-left: -20px;
  }
}

