/* Start all of the resume containers*/
.resume-row{
    width: 100%;
    max-height: contain;
    overflow: hidden;
    /* min-height: 50vh; */
    /* background-color: white; */
    padding-bottom: 3.5%;
    border-bottom: 2px solid rgb(213, 212, 212);
}
.resume-col{
    overflow: hidden;
    background-color: whitesmoke; 
    /* border: 1px solid transparent; */
    /* min-height: 55vh; */
    margin-top: -1px;
    max-height:-webkit-fit-content;
    max-height:-moz-fit-content;
    max-height:fit-content; 
    padding-top: 3.5%; 
}
.resume-col.end{
    padding-bottom: 3%; 
}
.resume-row.end{
    border-bottom: 0px solid rgb(213, 212, 212);
    padding-bottom: 0%;
}
/* End resume containers */

.project-col{
    overflow: hidden;
    background-color: #2B2B2B; 
    /* border: 1px solid transparent; */
    color: rgb(203, 203, 203);
    font-weight: 600;
    /* min-height: 55vh; */
    margin-top: -1px;
    max-height:-webkit-fit-content;
    max-height:-moz-fit-content;
    max-height:fit-content; 
    padding-top: 3.5%; 
}
.proj-thumbnail{
    border-radius: 5px;
    display: block;
    margin:auto;
    height: 200px; 
    width: 80%;
    background-color: transparent;
    margin-bottom: 30px;
}
.proj-thumbnail--end{
    border-radius: 5px;
    display: block;
    margin:auto;
    height: 200px; 
    width: 80%;
    background-color: green;
    margin-bottom: 70px;
}
div.project-row{
    margin: auto;   
}

.mask{
    height: 200px;
    font-weight: 700;
    width: 100%;
    background-color: #B35B67; ;
    opacity: 0.0;
    color: white;
    font-size: 20px;
    text-align: center;
    padding-top: 70px;
}
.mask:hover{
    opacity: 0.9;
}

.proj-thumbnail--end.WW{
    background-image: url(/mywebsite/static/media/WW2.f0095a58.webp);
    background-size: cover;
    background-position: center;
    background-color: whitesmoke;
    background-blend-mode: darken;
}
.proj-thumbnail.SS{
    background-image: url(/mywebsite/static/media/my_spreadsheet.7ab20e47.png);
    background-size: cover;
    background-position: left;
    background-color: whitesmoke;
    /* background-blend-mode: darken; */
}
.proj-thumbnail.Smile{
    background-image: url(/mywebsite/static/media/smile.2a687310.png);
    background-size: cover;
    background-position: center;
}
.proj-thumbnail.STP{
    background-image: url(/mywebsite/static/media/stp.3fb0c8fa.png);
    background-size: cover;
    background-position: center;
}
.proj-thumbnail.EXT2{
    background-image: url(/mywebsite/static/media/tux.7fc34fdf.png);
    background-size: contain;
    background-position: center;
    background-color: whitesmoke;
    background-repeat: no-repeat;
    background-blend-mode: darken;
}
.proj-thumbnail.Site{
    background-image: url(/mywebsite/static/media/site.566b51f7.png);
    background-size: cover;
    background-position: center;
}
