/* Start actual resume text and info. */
.resume-title{
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    width: 100%;
    margin-bottom: 4px;
}
.resume-title-label{
    padding-top: 8px;
    font-weight: 500;
    font-size: 20px;
    border-bottom: 2px solid rgb(225, 41, 41);
    padding-bottom: 3px;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
}
.resume-body{
    /* background-color: lavender; */
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    text-align: left;
    width: 100%
}
.resume-body-title{
    font-size: 30px;
    font-weight: 600;
}
.resume-body-subscript{
    margin-top: 2px;
    font-size: 16px;
    margin-bottom: 18px;

}
.resume-body-text{
    margin-bottom: 30px;
    text-align: left;
}
/* End resume text and info. */
 

@media screen and (max-height: 500px){
    .header-text{
        margin-top: -20px;
    }
}

