}

@media screen and (max-width: 768px){
    .col-sm-3 {
        flex: 0 0 50%;
        min-width: 100%;
    }
    .col-sm-9 {
        flex: 0 0 50%;
        min-width: 100%;
    }
    /* .col-sm-4 {
        flex: 0 0 50%;
        min-width: 100%;
    } */

    .resume-title{
        padding: 25px;
    }
    .resume-title-label{
        font-size: 18px;
